* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  background-color: rgba(42, 46, 51, 0.733);
  font-family: "Fredoka", sans-serif;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}
.logo-ctn {
  width: 400px;
}
.logo {
  width: 100%;
}

.calculadora {
  width: 400px;
  height: auto;
  box-shadow: 0px 0px 40px 10px rgb(31, 30, 30);
}

.fila {
  display: flex;
  align-items: center;
  justify-content: center;
}

#eq {
  background-color: rgb(220, 166, 31);
  color: #000;
  font-size: 35px;
  position: relative;
  box-shadow: 0px 0px 40px 10px rgba(31, 30, 30, 0.864);
}

#eq:hover {
  background-color: rgb(242, 166, 52);
}
