.boton-contenedor {
  height: 83px;
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 28px;
  background-color: rgb(28, 32, 43);
  color: #fff;
  cursor: pointer;
  user-select: none;
  border: 1px solid rgba(49, 49, 49, 0.494);
  transition: all 250ms ease-in-out;
}

.boton-contenedor:hover {
  background-color: #6876e4;
}

.img-delete {
  width: 30%;
  filter: invert(100%);
}

.operador {
  background-color: #6876e4;
}

.operador:hover {
  background-color: #22273a;
}
